div{
  display: flex;
  justify-content: center;
}





#aidi{
  display: grid;
  grid-template-columns: 100px 100px 100px 100px;
  grid-template-rows: 100px 50px 50px 50px 50px;
  grid-template-areas: "p p p p"
                      ". . . ."
                       ". . . AC"
                       ". . . ."
                       ". . . delete"
                       
                       
  ;
}


.delete{
  grid-area: delete;
  
}
.AC{
  grid-area: AC;
}
.p{
  grid-area: p;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  /* justify-content: start;
  align-items: center; */
  padding-left: 5%;
  
}
#meore{
  font-style: italic;
  font-size: small;
  color: rgba(0, 0, 0, 0.7);
  background-color: white;
  width: fit-content;
  /* justify-content: end; */
}
#pirveli{
  
  padding: 0.2em;
  width: fit-content;
  color:  black;
  background-color: white
  
}
